'use strict';

document.addEventListener('DOMContentLoaded', function () {
  var picture = document.querySelectorAll('.js_picture');
  picture.forEach(function (elem) {
    elem.addEventListener("click", function (event) {
      elem.classList.toggle('picture-box__active');
    });
  });
  var text = document.querySelectorAll('.js_text-box');
  text.forEach(function (elem) {
    elem.addEventListener("click", function (event) {
      var picture = elem.previousElementSibling;
      picture.classList.toggle('picture-box__active');
    });
  });
});