'use strict';

document.addEventListener('DOMContentLoaded', function () {
  var spinner = document.querySelector('.spinner');
  var spinnerButton = document.querySelectorAll('.spinner-line');
  var spinerMenu = document.querySelector('.bar-container');

  var overflowHidden = function overflowHidden() {
    if (document.body.style.overflow === '') {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = '';
    }
  };

  var spinnerClassToggle = function spinnerClassToggle() {
    spinerMenu.classList.toggle("active");
    spinnerButton.forEach(function (elem) {
      elem.classList.toggle("active");
    });
  };

  spinner.addEventListener('click', function (event) {
    event.preventDefault();
    spinnerClassToggle();
    overflowHidden();
  });
  /**
   * smooth transition on anchor
   *
   */

  var value = localStorage.getItem('anchor');

  if (!(value === null)) {
    var id = value,
        top = $(id).offset().top;
    top = top - 120;
    $("body,html").animate({
      scrollTop: top
    }, 1500);
    localStorage.clear();
  }

  console.log(value);
  $("#menu-header-menu").on("click", "a", function (event) {
    var locationOrig = window.location.origin + '/';
    var locationHref = window.location.href;

    if (locationOrig === locationHref) {
      var href = $(event.target).attr('href');

      if (href[0] === '#') {
        event.preventDefault();

        if (window.screen.width <= 1024) {
          spinnerClassToggle();
          overflowHidden();
        }

        var id = $(this).attr("href"),
            top = $(id).offset().top;
        top = top - 156;
        $("body,html").animate({
          scrollTop: top
        }, 1500);
      }

      ;
    } else {
      var anchor = $(event.target).attr('href');
      localStorage.setItem('anchor', anchor);
      window.location.href = window.location.origin + '/';
    }

    ;
  });
  $(".anchor").on("click", function (event) {
    event.preventDefault();

    if (window.screen.width <= 1024) {
      spinnerClassToggle();
      overflowHidden();
    }

    var id = $(this).attr("href"),
        top = $(id).offset().top;
    top = top - 156;
    $("body,html").animate({
      scrollTop: top
    }, 1500);
  });
});